.footer{
	padding: 35px 0 40px;
	background: #e3e3e3;
	@nest &__inner{
		font-size: 0;
		@media (--sm){
			text-align: center;
		}
	}
	@nest &__item{
		width: 50%;
		display: inline-block;
		vertical-align: middle;
		@media (--sm){
			width: 100%;
			display: block;
		}
		@nest &--right{
			text-align: right;
			@media (--sm){
				text-align: center;
			}
		}
	}
	@nest & span{
		font-size: 14px;
		color: #282828;
		display: block;
		line-height: 28px;
		font-family: var(--font);
		@media (--sm){
			margin-bottom: 15px;
		}
	}
	@nest & a{
		margin-left: 35px;
		display: inline-block;
		text-decoration: none;
		@media (--sm){
			margin-left: 25px;
		}
		@nest & i{
			font-size: 18px;
			color: #2c89b2;
		}
	}
}