.scheme-block{
	position: relative;
	margin-bottom: 115px;
	padding-left: 105px;
	@media (--xl){
		padding-left: 100px;
	}
	@media (--lg){
		margin-bottom: 90px;
	}
	@media (--sm){
		width: 100%;
		max-width: 250px;
		text-align: left;
		display: inline-block;
	}
	@nest &--indent-sm{
		padding-left: 85px;
	}
	@nest & img{
		position: absolute;
		top: -15px;
		left: 0;
	}
	@nest & p{
		font-size: 20px;
		color: #353535;
		margin-bottom: 0;
		line-height: 25px;
		position: relative;
		text-transform: uppercase;
		@media (--xl){
			font-size: 14px;
			line-height: 20px;
		}
		@media (--md){
			font-size: 12px;
		}
		@nest & span{
			font-size: 26px;
			color: #5da0be;
			font-weight: 700;
			@media (--xl){
				font-size: 20px;
			}
			@media (--md){
				font-size: 17px;
			}
		}
	}
}