.advantages{
	padding: 65px 0 70px;
	position: relative;
	@media (--lg){
		padding-bottom: 0;
		background: #f8f8f8;
	}
	@nest &:before{
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		bottom: 0;
		width: 52%;
		z-index: -1;
		background: resolve('ad.jpg') no-repeat center;
		-webkit-background-size: cover;
		background-size: cover;
		@media (--lg){
			display: none;
		}
	}
	@nest &:after{
		position: absolute;
		content: '';
		top: 0;
		right: 0;
		bottom: 0;
		width: 48%;
		z-index: -1;
		background: resolve('ad2.jpg') no-repeat center;
		-webkit-background-size: cover;
		background-size: cover;
		@media (--lg){
			display: none;
		}
	}
	@nest &__content{
		clear: fix;
	}
	@nest &__left{
		float: left;
		width: 53%;
		padding-right: 80px;
		@media (--lg){
			float: none;
			width: 100%;
			padding-right: 0;
		}
	}
	@nest &__right{
		float: right;
		width: 47%;
		padding-top: 20px;
		@media (--lg){
			float: none;
			width: 100%;
		}
	}
	@nest & h3{
		font-size: 26px;
		color: #282828;
		margin: 85px 0 0;
		line-height: 35px;
		@media (--lg){
			margin-top: 50px;
		}
		@media (--md){
			font-size: 20px;
			line-height: normal;
		}
		@nest & span{
			color: #5da0be;
			font-weight: 700;
			text-transform: uppercase;
		}
	}
	@nest & p{
		font-size: 20px;
		color: #282828;
		line-height: 32px;
		margin: 85px 0 0;
		font-weight: 300;
		font-family: var(--font);
		@media (--lg){
			margin: 50px 0;
		}
		@media (--md){
			font-size: 18px;
			line-height: normal;
			margin-bottom: 30px;
		}
	}
}