.products-block{
	font-size: 0;
	width: 100%;
	display: table;
	padding: 45px 0;
	background: #f7f7f7;
	transition: box-shadow 300ms;
	@media (--sm){
		padding: 15px 15px 20px;
	}
	@nest &--bg{
		background: #fbfbfb;
	}
	@nest &__image{
		width: 60%;
		padding: 0 15px;
		text-align: center;
		position: relative;
		display: table-cell;
		vertical-align: middle;
		@nest &:after{
			content: '';
			height: 100%;
			display: inline-block;
			vertical-align: middle;
		}
		@media (--xl){
			width: 50%;
		}
		@media (--sm){
			width: 100%;
			padding: 0;
			display: block;
			margin-bottom: 20px;
		}
		@nest & img{
			display: inline-block;
			vertical-align: middle;
		}
	}
	@nest &__content{
		width: 40%;
		padding-right: 65px;
		display: table-cell;
		vertical-align: top;
		@media (--xl){
			width: 50%;
		}
		@media (--sm){
			width: 100%;
			display: block;
		}
	}
	@nest & h3{
		font-size: 16px;
		color: #464646;
		font-weight: 700;
		line-height: 24px;
		margin-bottom: 10px;
		text-transform: uppercase;
		transition: color 300ms;
		@nest & span{
			color: #599dbc;
			transition: color 300ms;
		}
	}
	@nest & h4{
		font-size: 14px;
		color: #9d9d9d;
		font-weight: 400;
		margin-bottom: 0;
		line-height: 20px;
		font-family: var(--font);
	}
	@nest & b{
		font-size: 16px;
		color: #464646;
		display: block;
		font-weight: 400;
		line-height: 24px;
		margin-bottom: 7px;
		font-family: var(--font);
	}
	@nest & p{
		font-size: 32px;
		color: #599dbc;
		font-weight: 700;
		line-height: 24px;
		margin-bottom: 40px;
		@media (--sm){
			margin-bottom: 20px;
		}
	}
	@nest & a{
		font-size: 16px;
		color: #282828;
		line-height: 17px;
		position: relative;
		text-decoration: none;
		letter-spacing: .32px;
		display: inline-block;
		vertical-align: middle;
		border-bottom: 1px solid #5da0be;
		transition: color 300ms, border-color 300ms;
		@nest &:hover{
			color: #018caa;
			border-color: #018caa;
		}
		@nest & i{
			position: absolute;
			content: '';
			top: 50%;
			right: -14px;
			color: #599dbc;
			transform: translateY(-50%);
		}
	}
	@nest &--shadow{
		position: relative;
		box-shadow: 0 5px 30px #d1d1d1;
		@nest & h3{
			color: #018caa;
			@nest & span{
				color: #018caa;
			}
		}
	}
	@nest &--sm{
		max-width: 300px;
		padding: 15px 30px 45px;
		@media (--xl){
			margin: 0 auto;
		}
		@media (--lg){
			padding: 15px 15px 20px;
		}
		@media (--sm){
			padding: 15px;
		}
		@nest & h4{
			margin-bottom: 10px;
		}
	}
	@nest &--sm &{
		@nest &__image{
			width: 100%;
			height: 180px;
			display: block;
			margin-bottom: 10px;
		}
		@nest &__content{
			width: 100%;
			display: block;
			padding-right: 0;
		}
	}
}