.main-form{
	clear: fix;
	background: #f8f8f8;
	position: relative;
	@nest &__left{
		float: left;
		width: 37.5%;
		background: resolve('form-bg.jpg') no-repeat center;
		-webkit-background-size: cover;
		background-size: cover;
		@media (--xl){
			display: none;
		}
	}
	@nest &__right{
		float: right;
		width: 62.5%;
		overflow: hidden;
		position: relative;
		padding: 65px 0 50px 240px;
		@media (--xl){
			width: 100%;
			float: none;
			padding-left: 0;
		}
		@media (--sm){
			padding: 35px 0;
			margin-top: 20px;
		}
		@nest &:before{
			position: absolute;
			content: '';
			bottom: -63px;
			left: -40px;
			background: resolve('b1.png');
			width: width('b1.png');
			height: height('b1.png');
			@media (--xl){
				display: none;
			}
		}
		@nest &:after{
			position: absolute;
			content: '';
			top: 0;
			right: 0;
			background: resolve('f1.png');
			width: width('f1.png');
			height: height('f1.png');
			@media (--xl){
				display: none;
			}
		}
	}
	@nest &__girl{
		position: absolute;
		bottom: 0;
		left: 27%;
		z-index: 10;
		font-size: 0;
		@media (--xl){
			display: none;
		}
	}
	@nest & form{
		font-size: 0;
		width: 100%;
		z-index: 50;
		max-width: 615px;
		padding-right: 15px;
		position: relative;
		@media (--xl){
			max-width: 630px;
			padding: 0 15px;
			margin: auto;
		}
	}
	@nest & p{
		font-size: 20px;
		color: #050000;
		font-weight: 300;
		line-height: 29px;
		margin: 50px 0 45px;
		font-family: var(--font);
		@media (--sm){
			font-size: 16px;
			line-height: 24px;
			margin: 25px 0;
		}
	}
	@nest & input{
		font-size: 16px;
		color: #022828;
		height: 36px;
		border: 0;
		outline: none;
		font-weight: 300;
		margin-right: 40px;
		display: inline-block;
		background: transparent;
		width: calc(50% - 20px);
		font-family: var(--font);
		border-bottom: 1px solid #d9dada;
		transition: border-color 300ms;
		@nest &:focus{
			border-color: #74c7da;
		}
		@nest &:nth-child(4){
			margin-right: 0;
			@media (--sm){
				margin-bottom: 0;
			}
		}
		@nest &::placeholder{
			font-size: 16px;
			color: #022828;
		}
		@media (--sm){
			width: 100%;
			margin-right: 0;
			margin-bottom: 20px;
		}
	}
	@nest & button{
		display: block;
		border: 0;
		padding: 0;
		outline: none;
		height: 46px;
		color: #fff;
		width: 100%;
		font-size: 16px;
		max-width: 220px;
		font-weight: 500;
		position: relative;
		background: #039ebf;
		border-radius: 23px;
		margin: 35px 0 0 auto;
		font-family: var(--font);
		@nest &:after{
			position: absolute;
			content: '';
			z-index: -1;
			left: 50%;
			opacity: 0;
			bottom: -30px;
			transform: translateX(-50%);
			background: resolve('shadow.png');
			width: width('shadow.png');
			height: height('shadow.png');
			transition: opacity 300ms;
		}
		@nest &:hover{
			@nest &:after{
				opacity: 1;
			}
		}
		@media (--sm){
			margin: 35px auto 0;
		}
	}
}