.nav{
	max-width: 520px;
	margin: 10px 65px 0 0;
	display: inline-block;
	vertical-align: top;
	@media (--xl){
		max-width: 450px;
		margin-right: 30px;
	}
	@media (--lg){
		max-width: 100%;
		display: block;
		margin: 0 0 15px;
	}
	@nest & ul{
		margin: 0;
		padding: 0;
		width: 100%;
		display: table;
		list-style: none;
		@media (--lg){
			display: block;
		}
	}
	@nest & li{
		width: 1%;
		display: table-cell;
		text-align: center;
		white-space: nowrap;
		@media (--lg){
			width: 100%;
			display: block;
			margin-bottom: 5px;
			white-space: normal;
		}
	}
	@nest & a{
		font-size: 14px;
		color: #050000;
		position: relative;
		display: inline-block;
		text-decoration: none;
		letter-spacing: .28px;
		text-transform: uppercase;
		@nest &:after{
			position: absolute;
			content: '';
			bottom: 0;
			left: 0;
			right: 0;
			width: 0;
			height: 1px;
			transition: width 300ms;
			background: color(#050000 alpha(50%));
		}
		@nest &:hover{
			@nest &:after{
				width: 100%;
			}
		}
	}
}