.products{
	@nest &__inner{
		font-size: 0;
		margin-top: 40px;
		@nest &--indent-bot{
			margin-bottom: 55px;
		}
	}
	@nest &__item{
		width: 50%;
		display: inline-block;
		vertical-align: top;
		@media (--lg){
			width: 100%;
			display: block;
			max-width: 600px;
			margin: 0 auto 20px;
		}
		@nest &--sm{
			width: 25%;
			@media (--xl){
				width: 50%;
				margin-bottom: 20px;
				display: inline-block;
			}
			@media (--lg){
				padding: 0 10px;
			}
			@media (--sm){
				width: 100%;
			}
		}
	}
}