.modal{
	display: none;
	max-width: 600px;
	background: #fff;
	position: relative;
	text-align: center;
	padding: 60px 75px 75px;
	box-shadow: 0px 6px 16.56px 1.44px rgba(0, 68, 100, 0.15);
	@media (--sm){
		padding: 20px;
	}
	@nest &__btn{
		position: absolute;
		content: '';
		top: 20px;
		right: 20px;
		border: 0;
		padding: 0;
		outline: none;
		background: transparent;
		@nest & i{
			font-size: 28px;
			color: #599dbc;
		}
	}
	@nest & h2{
		font-size: 34px;
		color: #599dbc;
		font-weight: 700;
		padding: 0 50px;
		line-height: normal;
		margin-bottom: 25px;
		text-transform: uppercase;
		@media (--sm){
			font-size: 21px;
			margin-bottom: 15px;
		}
	}
	@nest & p{
		font-size: 20px;
		color: #636363;
		line-height: 26px;
		margin-bottom: 55px;
		@media (--sm){
			font-size: 18px;
			line-height: 23px;
			margin-bottom: 30px;
		}
	}
	@nest & .main-form{
		background: transparent;
		@nest & input{
			width: 100%;
			margin-bottom: 30px;
		}
		@nest & button{
			max-width: 315px;
			margin: 50px auto 0;
			@media (--sm){
				margin-top: 0;
				@nest &:after{
					display: none;
				}
			}
		}
	}
}