.billboard{
	position: relative;
	padding: 90px 0 110px;
	@media (--xl){
		text-align: center;
	}
	@nest &:before{
		position: absolute;
		content: '';
		top: 0;
		right: 0;
		bottom: 0;
		width: 50%;
		height: 100%;
		background: #e2e2e2;
		@media (--xl){
			display: none;
		}
	}
	@nest &:after{
		position: absolute;
		content: '';
		top: -55px;
		left: 50%;
		margin-left: -390px;
		background: resolve('billboard-img.png');
		width: width('billboard-img.png');
		height: height('billboard-img.png');
		@media (--xl){
			display: none;
		}
	}
	@nest &__item{
		position: absolute;
		content: '';
		font-size: 0;
		@nest &--first{
			left: 0;
			bottom: 0;
		}
		@nest &--last{
			top: 0;
			right: 0;
			@media (--xl){
				display: none;
			}
		}
	}
	@nest & .title{
		display: inline-block;
	}
	@nest & a{
		font-size: 16px;
		color: #282828;
		width: 100%;
		display: block;
		font-weight: 500;
		max-width: 197px;
		margin-top: 55px;
		position: relative;
		text-align: center;
		border-radius: 23px;
		text-decoration: none;
		padding: 14px 0 12px;
		font-family: var(--font);
		border: 1px solid #599dbc;
		transition: color 300ms, background 300ms;
		@nest &:hover{
			color: #fff;
			background: #18a1c1;
		}
		@media (--xl){
			margin-left: auto;
			margin-right: auto;
		}
	}
}