.equipment-block{
	display: block;
	background: #fff;
	overflow: hidden;
	position: relative;
	margin-bottom: 40px;
	text-decoration: none;
	padding: 35px 30px 0 45px;
	transition: background 300ms;
	@nest &:hover{
		background: #d9f1f5;
	}
	@media (--lg){
		border: 1px solid #f6f6f6;
	}
	@media (--md){
		text-align: center;
		padding: 25px 25px 0;
	}
	@nest &:after{
		position: absolute;
		content: '';
		background: resolve('eq-after.png');
		width: width('eq-after.png');
		height: height('eq-after.png');
		@media (--md){
			display: none;
		}
	}
	@nest & span{
		font-size: 24px;
		color: #5da0be;
		font-weight: 700;
		display: block;
		text-transform: uppercase;
	}
	@nest & img{
		position: absolute;
		@media (--md){
			position: static;
		}
	}
	@nest &--first{
		min-height: 430px;
		@media (--md){
			min-height: 0;
		}
		@nest &:after{
			left: 70px;
			bottom: -95px;
		}
		@nest & img{
			top: 90px;
			right: -1px;
			@media (--from-lg) and (--xl){
				max-width: 70%;
				right: auto;
				left: 50%;
				transform: translateX(-50%);
			}
		}
	}
	@nest &--sec{
		min-height: 195px;
		@media (--md){
			min-height: 0;
		}
		@nest &:after{
			left: -90px;
			bottom: -154px;
			background: resolve('eq-after2.png');
			width: width('eq-after2.png');
			height: height('eq-after2.png');
		}
		@nest & img{
			top: 0;
			right: 28px;
			@media (--from-lg) and (--xl){
				right: 0;
				max-width: 20%;
			}
		}
	}
	@nest &--last{
		padding-top: 25px;
		min-height: 195px;
		text-align: right;
		@media (--md){
			min-height: 0;
			text-align: center;
		}
		@nest &:after{
			right: 37px;
			bottom: -144px;
			background: resolve('eq-after2.png');
			width: width('eq-after2.png');
			height: height('eq-after2.png');
		}
		@nest & img{
			top: 5px;
			left: 10px;
			@media (--from-lg) and (--xl){
				max-width: 40%;
			}
		}
	}
}