.header{
  z-index: 50;
  background: #fff;
  position: relative;
	padding: 20px 0 18px;
  box-shadow: 0px 6px 16.56px 1.44px rgba(0, 68, 100, 0.15);
  @nest &__right{
  	text-align: right;
  	margin-top: 10px;
  	position: static;
  	@media (--lg){
  		margin-top: 0;
  		text-align: center;
  	}
  }
  @nest &__cont{
  	text-align: left;
  	display: inline-block;
  	@media (--lg){
  		display: block;
  		text-align: center;
  	}
  	@nest & a{
  		font-size: 24px;
  		color: #599dbc;
  		font-weight: 700;
  		display: block;
  		line-height: normal;
  		text-decoration: none;
  	}
  	@nest & span{
  		font-size: 14px;
  		color: #747474;
  		display: block;
  		letter-spacing: .28px;
  	}
  }
}