.companies-slider{
	margin-top: 75px;
	@media (--sm){
		margin-top: 40px;
	}
	@nest &__item{
		outline: none;
		@nest & a{
			font-size: 0;
			height: 75px;
			display: block;
			outline: none;
			position: relative;
			@media (--xl){
				text-align: center;
			}
			@nest &:after{
				content: '';
				width: 1px;
				height: 100%;
				display: inline-block;
				vertical-align: middle;
			}
			@nest & img{
				display: inline-block;
				vertical-align: middle;
			}
		}
	}
	@nest & .slick-arrow{
		position: absolute;
		content: '';
		top: -115px;
		right: 0;
		width: 42px;
		height: 42px;
		border: 0;
		padding: 0;
		outline: none;
		font-size: 28px;
		color: #fff;
		background: #18a1c1;
		transition: background 300ms;
		@media (--sm){
			top: auto;
			bottom: -70px;
			left: 50%;
		}
		@nest &.slick-prev{
			right: 42px;
			@media (--sm){
				margin-left: -42px;
			}
			@nest &:hover{
				background: color(#5da0be alpha(90%));
			}
		}
		@nest &.slick-next{
			background: color(#18a1c1 alpha(85%));
			@media (--sm){
				margin-right: 42px;
			}
			@nest &:hover{
				background: color(#5da0be alpha(50%));
			}
		}
	}
}