.equipment{
	padding: 70px 0 10px;
	background: resolve('eq-bg.jpg') no-repeat top center;
	-webkit-background-size: cover;
	background-size: cover;
	@nest &__wrap{
		clear: fix;
		margin-top: 50px;
	}
	@nest &__left{
		width: 50%;
		float: left;
		@media (--lg){
			width: 100%;
			float: none;
		}
	}
	@nest &__right{
		width: 46%;
		float: right;
		@media (--lg){
			width: 100%;
			float: none;
		}
	}
}