.advantages-item{
	margin-bottom: 75px;
	text-align: center;
	@media (--md){
		margin-bottom: 50px;
	}
	@nest & img{
		margin-bottom: 35px;
	}
	@nest & span{
		font-size: 20px;
		color: #3c3c3c;
		display: block;
		line-height: 23px;
		text-align: center;
		position: relative;
		@nest &:after{
			position: absolute;
			content: '';
			top: -21px;
			left: 50%;
			width: 32px;
			height: 2px;
			margin-left: -16px;
			background: #5da0be;
		}
	}
}