.feedback{
	position: relative;
	padding: 70px 0 100px;
	@media (--lg){
		padding: 60px 0 0;
	}
	@nest & span{
		display: block;
		font-size: 20px;
		color: #282828;
		margin: 60px 0 20px;
		line-height: normal;
		text-transform: uppercase;
		@media (--sm){
			font-size: 17px;
			margin: 30px 0 15px;
		}
	}
	@nest & p{
		font-size: 16px;
		color: #282828;
		margin-bottom: 0;
		line-height: 19px;
		padding-left: 20px;
		position: relative;
		font-family: var(--font);
		@nest &:last-child{
			margin-top: 20px;
		}
		@nest & i{
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			font-size: 16px;
			color: #5b9fbd;
			font-style: normal;
		}
		@nest & a{
			color: #282828;
			text-decoration: none;
		}
	}
}