.title{
	font-size: 30px;
	color: #4d4d4d;
	padding: 0 30px;
	font-weight: 700;
	line-height: 42px;
	position: relative;
	margin-bottom: 0;
	text-transform: uppercase;
	@nest &:after{
		position: absolute;
		content: '';
		top: 16px;
		left: 2px;
		width: 19px;
		height: 3px;
		background: #5da0be;
		@media (--sm){
			top: 13px;
		}
	}
	@media (--md){
		font-size: 25px;
		line-height: 35px;
	}
	@media (--sm){
		font-size: 20px;
		line-height: 30px;
	}
}